	export default {
    components: {
    	uniPopup
    },
		data() {
			return {
				tabIndex:0,
				tabBars: [{
					name: '本月',
					id: '0'
				}, {
					name: '本季度',
					id: '1'
				}, {
					name: '本年度',
					id: '2'
				}],
        showPopup: false,
        popType:'bottom',
        popState:0, //1,2,3
        msg:'',
        msgLength:0,
        pwdInpShow:true,
			};
		},
    watch:{
      msg(curVal,oldval){
        console.log("ryy-msg", curVal,oldval)
        
        if(/[^\d]/g.test(curVal)){
          this.msg = this.msg.replace(/[^\d]/g,'')+'';
        }else{
          this.msgLength = curVal.length;
        }
        console.log("ryy-msg2", this.msg)
      },
    },
    methods: {
      changeInp(e){
        console.log("e", e)
        return false
      },
      sendPwd(){
        console.log("sendPwd")
      },
    	topTab(index) { //点击tab-bar
    		this.tabIndex = index;
    	},
      //展示居中 popup
      showMiddlePopup() {
      	this.hidePopup();
      	this.popType = 'bottom';
      	this.showPopup = true;
      },
      //统一的关闭popup方法
      hidePopup() {
      	this.showPopup = false;
        this.popState=1
      },
      tixianHandle(state){
        this.popState = state+1
        if (this.popState==4) {
        	this.hidePopup()
        }
      }
    },
	}
